.body{

  font-family: "Montserrat", sans-serif;
}

label{
  font-family: "Montserrat", sans-serif;
}

div{
  font-family: "Montserrat", sans-serif;
}

.container-sc{
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 1fr 8fr 1fr;
  gap: 5px;

}

.header-sc {
  background-color: #fca311;
  grid-row: 1;
  grid-column: 1 /-1 ;

}


.header-sc h1 {
  color: #fff;
  font-weight: 200;
  font-family: "Montserrat", sans-serif;
  margin: 20px;
}


.content-sc{
  grid-row:2 ;
  grid-column: 1;
}

.footer{
  background-color: #14213d;
  grid-row: 3;
  grid-column: 1 / -1;
}


 .card-income {
  border-radius: 4px;
  position: relative;
  padding: 25px 15px;
  background-color: white;
  margin: 40px 5px;
  height: 400px;
  width: auto;
  box-shadow: 0 2px 5px #ccc;
  text-align: center;

}

.card-income input{
  margin: 40px;
  width:70%;
  border:2px solid #aaa;
  border-radius:4px;
  margin:8px 0;
  outline:none;
  padding:8px;
  box-sizing:border-box;
  transition:.3s;
  text-align: center;
  font-size: 20px;
}

.card-income input:focus{
  border-color:dodgerBlue;
  box-shadow:0 0 8px 0 dodgerBlue;

}

.card-income p{
  font-size: 20px;
  
}

.card-income button{

    width: 70%;
    height: 40px;
    padding: 16px 24px;
    border-radius: 4px;
    border: none;
    background: #141414;
    color: #fff;
    font-size:12px;
    text-align: center;
    margin: 30px;
  
  
}

.button-demography{
  width: 130px;
  height: 40px;
  padding: 16px 24px;
  border-radius: 4px;
  border: none;
  background: #141414;
  color: #fff;
  font-size:12px;
  text-align: center;

}

.card-demogrphy{
  display: grid;
  gap: 4px;
  
}

.container-sc .income-grid{
  display: grid;
  gap: 10px;

 }

.container-sc .grid-style-2 {
  display: grid;
  gap: 10px;
  grid-template-columns: 3fr 3fr 3fr;
  width: 100%; 
  
  }
  .container-sc .grid-box{
    display: grid;
    gap: 10px;
    grid-template-columns: 4fr 1fr; 
  }



  .card-2 {
    border-radius: 10px;
    position: relative;
    padding: 25px 15px;
    background-color: white;
    margin: 40px 5px;
    height: 50px;
    width: 300px;
    box-shadow: 0 2px 5px #ccc;
    font-size: 14px;
    text-align: left;
  }


  .card-2 .slider{
    height: 40px; 
    width: 220px;
    display: flex;
    align-items: center;
    margin-right: 15px;
    
    }

    .card {
      border-radius: 4px;
      position: relative;
      padding: 25px 15px;
      background-color: #f8f9fa;
      margin: 10px 20px;
      height: 50px;
      width: 90%;
      box-shadow: 0 2px 5px #ccc;
      font-family: "Montserrat", sans-serif;
      font-size: 14px;
      text-align: left;
    }

    .card-radiobtn-2{
      height: 90px;

    }
    .card-radiobtn-3{
      height: 140px;

    }


    
.card input{
  margin: 10px 0 0 0;
  border:2px solid #aaa;
  border-radius:4px;
  outline:none;
  padding:4px;
  box-sizing:border-box;
  transition:.3s;
}


    .calculate{
      border-radius: 4px;
      position: relative;
      padding: 25px 15px;
      background-color: white;
      margin: 10px 0;
      height: 70vh;
      width: 400px;
      box-shadow: 0 2px 5px #ccc;
      font-family: "Montserrat", sans-serif;
      font-size: 14px;
      text-align: center;
      justify-content: center;
    }

    .img{
      width: 60px;
      height: 60px;
      justify-content: center;
    }

    .income-group{
      margin: 20px 0 0 0;
    }

    .total-expenditure{
      margin: 20px 0 0 0;
    }



    .tab-list {  border-bottom: 1px solid #ccc;
      padding-left: 0;
    }
    
    .tab-list-item {
      display: inline-block;
      list-style: none;
      margin-bottom: -1px;
      padding: 0.5rem 0.75rem;
    }
    
    .tab-list-active {
      background-color: white;
      border: solid #ccc;
      border-width: 1px 1px 0 1px;
    }

    .food-policy-container{
      background-color: white;
      padding: 60px;

    }
    .scroll {
      width: 100%;
      height: 70vh;
      background: #e5e5e5;
      overflow: auto;
   }
   .scroll::-webkit-scrollbar {
       width: 12px;
   }
   
   .scroll::-webkit-scrollbar-track {
       -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
       border-radius: 10px;
   }
   
   .scroll::-webkit-scrollbar-thumb {
       border-radius: 10px;
       -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
   }




    

    .box{
      display: flex;
      align-items: center;
      justify-content: center;
  
 
    }
  
    .box .value.left{
      left: -22px;
    }
    .box .value.right{
      right: -43px;
    }

